import React from 'react';
import { View, Pressable, Text, StyleSheet, useColorScheme } from 'react-native';
import { useNavigate } from 'react-router-dom';
import { colors, spacing, typography } from '../assets/styles';

const SeasonSelector = ({ selectedSeason }) => {
  const colorScheme = useColorScheme();
  const themeColors = colors[colorScheme];
  const navigate = useNavigate();

  return (
    <View style={styles.container}>
      {[1, 2, 3, 4].map((season) => (
        <Pressable
          key={season}
          onPress={() => navigate(`/season/${season}`)}
          style={styles.button}
        >
          <Text
            style={[
              styles.text,
              { color: season === selectedSeason ? themeColors.accent : themeColors.text },
            ]}
          >
            Season {season}
          </Text>
          {season === selectedSeason && (
            <View style={[styles.underline, { backgroundColor: themeColors.accent }]} />
          )}
        </Pressable>
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'center',
    paddingVertical: spacing.medium,
    gap: spacing.large,
  },
  button: {
    paddingVertical: spacing.small,
    paddingHorizontal: spacing.large,
    borderRadius: 20,
    alignItems: 'center',
  },
  text: {
    ...typography.body,
    fontSize: 16,
    fontWeight: 'bold',
    textTransform: 'uppercase',
  },
  underline: {
    height: 4,
    width: '80%',
    borderRadius: 2,
    marginTop: 5,
  },
});

export default SeasonSelector;