import React, { useRef, useEffect } from 'react';
import { View, Text, StyleSheet, ScrollView, Pressable, useColorScheme } from 'react-native';
import { useParams, useNavigate } from 'react-router-dom';
import { colors, spacing, typography, shadows } from '../assets/styles';

import season1Episodes from '../data/seasons/season1/episodes';
import season2Episodes from '../data/seasons/season2/episodes';
import season3Episodes from '../data/seasons/season3/episodes';
import season4Episodes from '../data/seasons/season4/episodes';

const TranscriptScreen = () => {
  const scrollRef = useRef(null);
  const { seasonId, episodeId } = useParams();
  const navigate = useNavigate();
  const colorScheme = useColorScheme();
  const themeColors = colors[colorScheme];

  useEffect(() => {
    window.scrollTo({ top: 0 });
  }, []);

  const getEpisodes = (season) => {
    switch(season) {
      case 1: return season1Episodes;
      case 2: return season2Episodes;
      case 3: return season3Episodes;
      case 4: return season4Episodes;
      default: return season1Episodes;
    }
  };

  const episode = getEpisodes(parseInt(seasonId, 10))
    .find(ep => ep.number === parseInt(episodeId, 10));

  const transcript = episode?.transcript?.trim() || '';
  const cleanedTranscript =
    transcript.startsWith('[') && transcript.endsWith(']')
      ? transcript.slice(1, transcript.length - 1)
      : transcript;

      const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      };
      
      

  if (!episode) {
    return (
      <View style={[styles.container, { backgroundColor: themeColors.background }]}>
        <Text style={[styles.title, { color: themeColors.text }]}>
          Episode not found
        </Text>
      </View>
    );
  }

  return (
    <View style={[styles.container, { backgroundColor: themeColors.background }]}>
      <ScrollView ref={scrollRef} contentContainerStyle={styles.scrollContainer}>
        <View style={[styles.headerContainer, { borderBottomColor: themeColors.text }]}>
          <Pressable
            onPress={() => navigate(-1)}
            style={({ pressed }) => [styles.backButton, { opacity: pressed ? 0.8 : 1 }]}
          >
            <Text style={[styles.backText, { color: themeColors.text }]}>← Back</Text>
          </Pressable>
          <View style={styles.headerTextContainer}>
            <Text style={[styles.title, { color: colorScheme === 'dark' ? '#fff' : themeColors.header }]}>
              {episode.title}
            </Text>
            <Text style={[styles.subtitle, { color: themeColors.text }]}>
              Season {seasonId} - Episode {episode.number}
            </Text>
          </View>
        </View>
        <View style={[styles.transcriptContainer, shadows.card, { backgroundColor: themeColors.card }]}>
          <Text style={[styles.transcript, { color: themeColors.text }]}>
            {cleanedTranscript}
          </Text>
        </View>
        <Pressable
          onPress={scrollToTop}
          style={({ pressed }) => [
            styles.backToTopButton,
            {
              opacity: pressed ? 0.8 : 1,
              backgroundColor: colorScheme === 'dark' ? themeColors.accent : '#e6e3e3',
            }
          ]}
        >
          <Text style={[styles.backToTopText, { color: themeColors.text }]}>↑ Back to top</Text>
        </Pressable>
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContainer: {
    padding: spacing.large,
    paddingBottom: spacing.xlarge,
  },
  headerContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: spacing.small,
    borderBottomWidth: 1,
    paddingBottom: spacing.small,
    position: 'relative',
  },
  backButton: {
    position: 'absolute',
    left: spacing.large,
  },
  backText: {
    ...typography.body,
    fontWeight: 'bold',
  },
  headerTextContainer: {
    alignItems: 'center',
  },
  title: {
    ...typography.header,
    fontSize: 28,
  },
  subtitle: {
    ...typography.body,
    fontSize: 16,
    marginTop: spacing.tiny,
  },
  transcriptContainer: {
    padding: spacing.large,
    borderRadius: 10,
    marginTop: spacing.large,
  },
  transcript: {
    ...typography.body,
    lineHeight: 24,
  },
  backToTopButton: {
    marginTop: spacing.large,
    padding: spacing.medium,
    borderRadius: 10,
    alignItems: 'center',
  },
  backToTopText: {
    ...typography.body,
    fontWeight: 'bold',
  },
});

export default TranscriptScreen;