import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet, ScrollView, useColorScheme, Dimensions, Image } from 'react-native';
import { useParams, useNavigate } from 'react-router-dom';
import EpisodeCard from '../components/EpisodeCard';
import SeasonSelector from '../components/SeasonSelector';
import { colors, spacing, typography, shadows } from '../assets/styles';

import season1Episodes from '../data/seasons/season1/episodes';
import season2Episodes from '../data/seasons/season2/episodes';
import season3Episodes from '../data/seasons/season3/episodes';
import season4Episodes from '../data/seasons/season4/episodes';

const HomeScreen = () => {
  const { seasonId } = useParams();
  const navigate = useNavigate();
  const [windowWidth, setWindowWidth] = useState(Dimensions.get('window').width);
  const colorScheme = useColorScheme();
  const themeColors = colors[colorScheme];

    useEffect(() => {
      window.scrollTo({ top: 0 });
    }, []);
  

  useEffect(() => {
    const subscription = Dimensions.addEventListener('change', ({ window }) => {
      setWindowWidth(window.width);
      console.log(window.width);
    });
    return () => subscription?.remove();
  }, []);

  const getEpisodes = (season) => {
    switch(season) {
      case 1: return season1Episodes;
      case 2: return season2Episodes;
      case 3: return season3Episodes;
      case 4: return season4Episodes;
      default: return season1Episodes;
    }
  };

  return (
    <View style={[styles.container, { backgroundColor: themeColors.background }]}>
      <ScrollView
        contentContainerStyle={styles.scrollContainer}
        showsVerticalScrollIndicator={false}
      >
        <View style={styles.heroContainer}>
          <Image
            source={require('../assets/hero-image 2.jpg')}
            style={styles.heroImage}
            resizeMode="cover"
          />
          <View style={styles.heroOverlay} />
          <View style={[
            windowWidth >= 1190 || windowWidth <= 990 ? styles.heroTextContainerLarge : styles.heroTextContainerSmall,
            windowWidth >= 850 ? { left: windowWidth * 0.5, alignItems: 'flex-start' } : styles.heroTextCenter
          ]}>
            <View style={styles.textStack}>
              <Text style={[styles.heroTitle, { fontSize: windowWidth >= 990 ? 65 : 55 }]}>
                The Good Place
              </Text>
              <Text style={[styles.heroTitle, { fontSize: windowWidth >= 990 ? 65 : 55 }]}>
                Transcripts
              </Text>
            </View>
          </View>
        </View>

        <SeasonSelector selectedSeason={parseInt(seasonId, 10)} />

        <View style={[styles.grid, windowWidth < 768 && styles.mobileGrid]}>
          {getEpisodes(parseInt(seasonId, 10)).map((episode) => (
            <EpisodeCard
              key={`s${seasonId}/e${episode.number}`}
              episode={episode}
              onPress={() => navigate(`/season/${seasonId}/episode/${episode.number}`)}
            />
          ))}
        </View>
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollContainer: {
    flexGrow: 1,
  },
  heroContainer: {
    height: 450,
    position: 'relative',
  },
  heroImage: {
    width: '100%',
    height: '100%',
    backgroundColor: '#333',
  },
  heroOverlay: {
    ...StyleSheet.absoluteFillObject,
    backgroundColor: 'rgba(0,0,0,0.7)',
  },
  heroTextContainerLarge: {
    position: 'absolute',
    top: '25%',
    padding: spacing.large,
  },
  heroTextContainerSmall: {
    position: 'absolute',
    top: '15%',
    padding: spacing.large,
  },
  heroTextCenter: {
    left: 0,
    right: 0,
    alignItems: 'center',
  },
  heroTitle: {
    ...typography.header,
    color: 'white',
    textShadow: '2px 2px 4px rgba(0, 0, 0, 0.6)',
    marginBottom: spacing.medium,
  },
  grid: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    padding: spacing.large,
  },
  mobileGrid: {
    flexDirection: 'column',
    alignItems: 'center',
  },
  textStack: {
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
  }
});

export default HomeScreen;
