import React, { useState } from 'react';
import { View, Text, Pressable, StyleSheet, useColorScheme } from 'react-native';
import { colors, spacing, typography, shadows } from '../assets/styles';

const EpisodeCard = ({ episode, onPress }) => {
  const [isHovered, setIsHovered] = useState(false);
  const colorScheme = useColorScheme();
  const themeColors = colors[colorScheme];

  return (
    <Pressable
      onHoverIn={() => setIsHovered(true)}
      onHoverOut={() => setIsHovered(false)}
      onPress={onPress}
      style={({ pressed }) => [
        styles.card,
        {
          backgroundColor: themeColors.card,
          transform: [{ translateY: isHovered ? -5 : 0 }],
          opacity: pressed ? 0.8 : 1,
          ...(isHovered ? shadows.hover : shadows.card),
        }
      ]}
    >
      <Text style={[styles.number, { color: themeColors.text }]}>
        Episode {episode.number}
      </Text>
      <Text style={[styles.title, { color: themeColors.text }]}>
        {episode.title}
      </Text>
    </Pressable>
  );
};

const styles = StyleSheet.create({
  card: {
    width: 280,
    margin: spacing.medium,
    padding: spacing.large,
    borderRadius: 10,
  },
  number: {
    ...typography.caption,
    marginBottom: spacing.small,
  },
  title: {
    ...typography.title,
    marginTop: spacing.small,
  },
});

export default EpisodeCard;