import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import HomeScreen from './components/HomeScreen';
import TranscriptScreen from './components/TranscriptScreen';

const App = () => {
  return (
    <Router>
      <Routes>
        <Route path="/season/:seasonId" element={<HomeScreen />} />
        <Route path="/season/:seasonId/episode/:episodeId" element={<TranscriptScreen />} />
        <Route path="/" element={<Navigate to="/season/1" replace />} />
        <Route path="*" element={<Navigate to="/season/1" replace />} />
      </Routes>
    </Router>
  );
};

export default App;